import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
export interface ISlides {
  url: string;
  title: string;
  description: string;
}
@Component({
    selector: 'rm-carousel',
    imports: [CommonModule],
    template: `
    <div class="slides">
      <div
        class="slide"
        [ngStyle]="{
          'background-image': 'url(' + slides[currentSlide].url + ')',
          opacity: hidden ? 0 : 1,
          transition: 'opacity ' + animationSpeed + 'ms ease-in-out'
        }"
      ></div>

      @if (indicatorsVisible) {
      <div id="indicators">
        @for (slide of slides; track $index; ) {
        <div
          (click)="jumpToSlide($index)"
          class="indicator"
          [ngClass]="{ active: $index == currentSlide }"
        ></div>
        }
      </div>
      }

      <button (click)="next()" class="nextButton">></button>
      <button (click)="previous()" class="previousButton"><</button>
    </div>
  `,
    styles: `
    :host {
      display: block;
      width: 100%;

      .slides {
        width: 100%;
        height: 700px;
        border-radius: 10px;
        overflow: hidden;
        position: relative;

        .slide {
          width: 100%;
          height: 100%;
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          position: relative;
        }

        .nextButton,
        .previousButton {
          position: absolute;
          top: 50%;
          border: 0px;
          background-color: transparent;
          font-size: 40px;
          color: #fff;
          cursor: pointer;
        }

        .nextButton {
          right: 20px;
        }

        .previousButton {
          left: 20px;
        }

        #indicators {
          position: absolute;
          left: 0px;
          right: 0px;
          margin: auto;
          bottom: 20px;
          width: 200px;

          .indicator {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background-color: #fff;
            display: inline-block;
            margin: 0px 5px;
            cursor: pointer;
            transition: background-color 0.5s ease-in-out;

            &.active {
              background-color: #4f4f4f;
            }
          }
        }
      }
    }
  `
})
export class RmCarouselComponent implements OnInit, OnDestroy {
  @Input() slides: ISlides[] = [];
  @Input() indicatorsVisible = true;
  @Input() animationSpeed = 500;
  @Input() autoPlay = false;
  @Input() autoPlaySpeed = 3000;
  currentSlide = 0;
  // faArrowRight = faArrowRight;
  // faArrowLeft = faArrowLeft;
  hidden = false;
  jumpToSlideClearTime!: ReturnType<typeof setTimeout>;
  ngOnInitClearTime!: ReturnType<typeof setInterval>;

  next() {
    let currentSlide = (this.currentSlide + 1) % this.slides.length;
    this.jumpToSlide(currentSlide);
  }

  previous() {
    let currentSlide =
      (this.currentSlide - 1 + this.slides.length) % this.slides.length;
    this.jumpToSlide(currentSlide);
  }

  jumpToSlide(index: number) {
    this.hidden = true;
    this.jumpToSlideClearTime = setTimeout(() => {
      this.currentSlide = index;
      this.hidden = false;
    }, this.animationSpeed);
  }

  ngOnInit() {
    if (this.autoPlay) {
      this.ngOnInitClearTime = setInterval(() => {
        this.next();
      }, this.autoPlaySpeed);
    }
  }

  ngOnDestroy(): void {
    clearInterval(this.ngOnInitClearTime);
    clearTimeout(this.jumpToSlideClearTime);
  }
}
